import { Component, OnInit } from '@angular/core';
import { FormBuilder } from '@angular/forms';
import {MembershipService, CompanyService } from '../../../../shared/services/index';
import {Invoice} from '../../../../shared/models/invoice';

declare var jQuery : any;
declare var moment:any;
declare var window: any;

@Component({
  selector : 'og-invoice',
  template: require('./invoice.component.html'),
  styles: [require('./invoice.component.css'),require('./../../../../../assets/css/team-settings.css')]
})

export class InvoiceComponent implements OnInit {
  isInvoiceExist: boolean = false;
  allInvoices:any = [];
  invoiceNo : any;
  cardDetail: any;
  loadingInvDet:boolean = true;
  constructor(private fb : FormBuilder, private _membershipService : MembershipService, private _companyService: CompanyService) {
  };
  ngOnInit() {
    this.invoiceNo = 0;
    this.getInvoices();
    //this.getPaymentDetails();
  }

  getInvoices() {
    let self = this;
    self.loadingInvDet = true;
    this._membershipService.getInvoices()
          .subscribe(
                  (invoices : any) => {
                    self.isInvoiceExist = false;
                    self.allInvoices = [];
                    invoices.list.forEach((invoice:any)=>{
                      self.allInvoices.push(new Invoice(invoice.invoice));
                    });
                    if(self.allInvoices.length > 0)
                      self.isInvoiceExist = true;

                    self.loadingInvDet = false;
                    console.log('allInvoices',self.allInvoices);
                  },
                  (error:any) => {
                    //console.log('get invoice getErrro',error);
                    self.loadingInvDet = true;
                  }
            );
  }

  getInvoicePdf(invoiceId:any){
    let self = this;
    let getInvoicePdf = self._membershipService.getInvoicesPdf(invoiceId)
      .subscribe(
        (success: any) => {
          window.location.assign(success.download.download_url);
        },
        (error:any) => {
          getInvoicePdf.unsubscribe();
        }
      );
  }

  contactUs(){
    jQuery('.intercom-launcher').trigger('click');
  }

  //  getPaymentDetails() {
  //     this._membershipService.getPaymentDetails()
  //       .subscribe(
  //                 (data : any) => {
  //                   this.cardDetail = data.card_type + '(ending in '+ data.last4+')';
  //                 },
  //                 (error:any) => {
  //                   console.log('getErrro payment det',error);
  //                 }
  //             );
  // }
}



