import { Component, OnInit, EventEmitter, OnChanges , OnDestroy} from '@angular/core';
import {MembershipService, PlanService} from '../../../../shared/services/index';
import {Subscriptions} from '../../../../shared/models/currentPlan';
import {Plans} from '../../../../shared/models/plans';
import {Addon} from '../../../../shared/models/addons';
import {PlanFeatures} from '../../../../shared/models/planFeatures';
import {Estimate} from '../../../../shared/models/estimate';
import {NumberFormater } from '../../../../shared/services/helper-service/number-format';
import { Validators, FormBuilder, FormGroup} from '@angular/forms';
import { Subscription } from 'rxjs/Subscription';
import { CookieService, SubDomainService, CompanyService } from '../../../../shared/services/index';
declare var ga: any;
declare var _kmq: any;
declare var jQuery : any;
declare var window: any;
declare var fbq: any;
// declare var LeadDyno: any;

@Component({
	selector : 'og-subscription',
	template: require('./subscription.component.html'),
	styles: [require('./subscription.component.css'),require('./../../../../../assets/css/team-settings.css'),require('./../../../../../assets/css/sahil-hover.css'), require('./../../../../../assets/css/custom-material.css')],
	inputs:['payment_status','userRole'],
	outputs:['isChangePlan','changeToPlan']
})

export class SubscriptionComponent implements OnInit,OnChanges,OnDestroy {
	runningPlanSubscription: Subscriptions = new Subscriptions(null);
	runningPlanDetail: Plans = new Plans(null);
	runningPlan:string = '';
	runningPlanCycle: string = '';
	futurePlanSubscription: Subscriptions = new Subscriptions(null);
	futurePlanDetail: Plans = new Plans(null);
	allPlansList:any = [];
	addonList:any = [];
	viewplansList:any = [];
  enterprisePlan:any = '';
	viewPlan: string = 'm';
	starterPlanFeatures:any = [];
	businessPlanFeatures:any = [];
	enterprisePlanFeatures:any = [];
	upgradeToPlan: Plans = new Plans(null);
	upgradeBillingCycle:String = '';
	couponCode: String = '';
	couponCodeModal: String = '';
	estimation: any = {};
	loading: boolean = true;
	loadingPlans: boolean = true;
	loadingMemDet:boolean = true;
	couponForm:FormGroup;
	error:boolean = false;
	errorMessage: string = '';
	payment_status:String = '';
	userRole: string = 'MANAGER';
	plansTypes:any = [];
	planFeatures:any = [];
	isDowagrade:Boolean = false;
	show:boolean = true;
	isChangePlan = new EventEmitter<boolean>();
	changeToPlan = new EventEmitter<any>();
	private subs: Subscription[] = [];
	FreePlanName: string = '';
	runningPlanVisitLeads:any = {
		'leads':0,
		'visits':0
	}
	essentials_m:number = 0;
	business_m:number = 0;
  freelancer_m:number = 0;

	kmqAmountDue: any;
	kmqAmountPaid:any;
	kmqCreditsApplied: any;
	kmqTotalAmount:any;
	membershipCancel: boolean = false;
	is_admin_created:boolean = false;
	is_subcripion_cancelled :boolean = false;
	addonLeads:number = 0;
	addonTraffic:number = 0;
	allowedLeads:number = 0;
	allowedTraffic:any = 0;
	leadsUsed:number = 0;
	trafficUsed:number = 0;
	leadsUsedPercentage:any = 0;
	trafficUsedPercentage:any = 0;
	hasAddon:number = 0;
	currentCompanyDetails:any = '';
	constructor(private _membershipService: MembershipService,
		private _planService: PlanService,
		public fb:FormBuilder,
		private _cookieService : CookieService,
		private _companyService: CompanyService,
		private _subDomainService: SubDomainService) {
	};

	ngOnInit() {
		this.couponForm = this.fb.group({
			couponInput: [this.couponCodeModal,Validators.compose([Validators.required,Validators.minLength(5)])]
		});

		jQuery('.modal').on('hidden.bs.modal', function(){
			jQuery('.estimate-tabs').removeClass('active');
			jQuery('#subscription-tab').addClass('active');
			jQuery('#tab_default_1').addClass('active');
			jQuery('#error-UpgradePlan').html('').addClass('hide');
			this.couponCode = '';
			this.loading = true;
			this.error = false;
			this.errorMessage = '';
			this.upgradeBillingCycle = '';
		});
		//this.getPlansFeatures();
		this.userRole = this._cookieService.readCookie('role');
		if(localStorage.getItem('lodashAuthToken'))
			this.is_admin_created = JSON.parse(localStorage.getItem('lodashAuthToken')).is_admin_created;
		if(this.is_admin_created === undefined)
			this.is_admin_created =false;
		let sub_domain = this._subDomainService.subDomain.sub_domain;
    	let companyAccess = JSON.parse(this._cookieService.readCookie('filepicker_token_json'));
		let subscription_status = '';
		if (!companyAccess)
		window.location.href = window.location.origin + '/logout';
		else
		companyAccess.forEach((e: any) => {
			if (e.key === sub_domain) {
			subscription_status = e.value;
			}
		});
		if(subscription_status==='cancelled'){
		  jQuery('#membershipDetails').addClass('plan-cancelled');
		  jQuery('#smScrWrapperContent').css('display','block');
		}
	}

	getPlanSubscription(){
		let self = this;
		self.loadingMemDet = true;
		return self._membershipService.getplanSubscription()
			.subscribe(
				(success: any)=>{
					// console.log('getPlanSubscription',success);
					self.payment_status = success.currentplan.customer.card_status;
					self.runningPlanSubscription = new Subscriptions(success.currentplan.subscription);
					self.runningPlanDetail = new Plans(success.cpDetail.plan);
					if(self.runningPlanSubscription.plan_id !== 'starter' && self.runningPlanSubscription.plan_id !== 'freemium'){
						let cycle = self.runningPlanSubscription.plan_id.split('_');
						self.runningPlan = cycle[0];
						if(cycle[1] === 'm')
							self.runningPlanCycle = 'Monthly';
						else if(cycle[1] === 's')
							self.runningPlanCycle = 'Half Yearly';
						else if(cycle[1] === 'y')
							self.runningPlanCycle = 'Yearly';
						self.viewPlan = cycle[1];
					}

					if(success.futureplan){
						self.futurePlanSubscription = new Subscriptions(success.futureplan.subscription);
						self.futurePlanDetail = new Plans(success.fpDetail.plan);
					}else{
						self.futurePlanSubscription = null;
					}
					self.getViewPlans(self.viewPlan);
					self.getAddons();
					//self.loadingMemDet = false;
				},
				(error:any)=>{
					//getPlanSubscription.unsubscribe();
					self.loadingMemDet = true;
				}
			);
	}
	numberFormat(num:number){
		return NumberFormater.insertCommas(num);
	}
	SplitString(name:String){
	  if(name)
	    return name.split(' ')[1];
  }

	reActivate(){
	if(this.payment_status == 'no_card' && this.upgradeToPlan.id !== 'starter'){
		let data = {};
				data = {
					'billing':{
						'plan_id':this.upgradeToPlan.id,
						'prorate':true,
						'end_of_term':false,
						'status':false
					}
				};
			this.changeToPlan.emit(data);
			this.isChangePlan.emit(true);
			jQuery('#upgrade-plan-popup').modal('hide');
			setTimeout(function () {
				jQuery('#cc-modal').modal('show');
			}, 1000);
	}else{
		jQuery('.btnMakePayment').html('please Wait...').attr('disabled',true);
		let reactivateMembership = this._membershipService.activateNow()
			.subscribe(
				(success: any) => {
					window.toastNotification('You have Successfully Reactivated ');
					//this._cookieService.createCookie('filepicker_token_json', JSON.stringify(response.companyAccess), 3);
					let membership = JSON.parse(this._cookieService.readCookie('filepicker_token_json'));
					membership[1].value = success.subscription.status;
					this._cookieService.eraseCookie('filepicker_token_json');
					this._cookieService.createCookie('filepicker_token_json',JSON.stringify(membership),3);
					let storage = JSON.parse(this._cookieService.readCookie('storage'));
					storage.company.billing.chargebee_plan_id = success.subscription.plan_id;
					this._cookieService.eraseCookie('storage');
					this._cookieService.createCookie('storage',JSON.stringify(storage),3);
					window.location.reload();
				},
				(error:any) => {
					this.error = true;
					jQuery('.btnMakePayment').html('Make Payment').attr('disabled',false);
					this.errorMessage = 'Subscription cannot be re-activated as your card is decline';
					reactivateMembership.unsubscribe();
				}
			);
		}
	}

	getAllPlans(){
		let self = this;
		return self._membershipService.getPlans()
			.subscribe(
				(success: any) => {
					self.allPlansList = [];
					success.lists.list.forEach((list:any)=>{
					 	self.allPlansList.push(new Plans(list.plan));
					 	if(list.plan.id === 'essentials_m')
					 		self.essentials_m = list.plan.price/100;
					 	else if(list.plan.id === 'business_m')
					 		self.business_m = list.plan.price/100;
					 	else if(list.plan.id === 'freelancer_m')
					 		self.freelancer_m = list.plan.price/100;
					});
					self.planFeatures = [];
					success.plans.forEach((plan:any)=>{
						self.planFeatures.push(new PlanFeatures(plan));
					});
					self.getViewPlans(self.viewPlan);
				},
				(error:any) => {
					//console.log('getAllPlans error',error);
					//getAllPlans.unsubscribe();
				}
			);
	}
	getViewPlans(viewPlan:string = null){
		this.loadingPlans = true;
		if(viewPlan)
			this.viewPlan = viewPlan;
		this.viewplansList = [];
		if(this.allPlansList.length > 0){
			this.allPlansList.forEach((plan:any)=>{
				if(plan.id === 'starter' || plan.id === 'freemium')
					this.viewplansList.push(plan);
				if(plan.id.split('_')[0] !== 'enterprise' && plan.id.split('_')[1] === this.viewPlan)
					this.viewplansList.push(plan);
				if(plan.id.split('_')[0] === 'enterprise')
				  this.enterprisePlan = plan;

					});
					this.loadingPlans = false;
					this.FreePlanName = this.viewplansList[0].name;
				}
				else{
					this.subs.push(this.getAllPlans());
				}
				this.getAddonAppliedDetails();
	}

	getAddonAppliedDetails(){
		let self = this;
		let sub_domain = window.location.href.split('//')[1].split('.')[0];
	    this._companyService.isSubDomainExist(sub_domain)
	    	.subscribe(
	    		(success: any) => {
	    			localStorage.setItem('lodashAuthToken', JSON.stringify(success));
	    			self.currentCompanyDetails = success;
	    			this.runningPlanVisitLeads = {
	    				'leads':self.currentCompanyDetails.leads.total,
	    				'visits':self.currentCompanyDetails.traffic.frequency
	    			}
	    			self.allowedLeads = self.currentCompanyDetails.current_limit.leads;
					self.allowedTraffic = self.currentCompanyDetails.current_limit.traffic;
					self.addonLeads = self.currentCompanyDetails.addon.leads;
					self.addonTraffic = self.currentCompanyDetails.addon.traffic;
					self.hasAddon = self.currentCompanyDetails.addon.quantity;
					self.leadsUsed = self.currentCompanyDetails.current_usage.leads?self.currentCompanyDetails.current_usage.leads:0;
					self.trafficUsed = self.currentCompanyDetails.current_usage.traffic?self.currentCompanyDetails.current_usage.traffic:0;
					self.leadsUsedPercentage = ((self.leadsUsed/self.allowedLeads)*100).toFixed(2);
					self.trafficUsedPercentage = ((self.trafficUsed/self.allowedTraffic)*100).toFixed(2);
					jQuery('#leadsProgress').css('width',self.leadsUsedPercentage+'%');
					jQuery('#trafficProgress').css('width',self.trafficUsedPercentage+'%');
	    		},
	    		(error) => {
	            	if (error.error.code === 'E_COMPANY_NOT_FOUND')
	            		console.log('company not found');
	        	}
	        );
	}

	getAddons(){
		let self = this;
		return self._membershipService.getAddons()
		.subscribe(
			(success: any) => {
				self.addonList = [];
				success.list.forEach((addons:any)=>{
					self.addonList.push(new Addon(addons.addon));
				});
				self.loadingMemDet = false;
			},
			(error:any) => {
				this.error = true;
				self.loadingMemDet = false;
				console.log('addons error',error);
			}
		);
	}
	toCeil(pr:number){
		return Math.ceil(pr);
	}
	makePayment(){
		if(this.payment_status == 'no_card' && this.upgradeToPlan.id !== 'starter'){
			let data = {};
			if(this.upgradeToPlan.id === 'starter'){
				data = {
					'billing':{
						"plan_id":this.upgradeToPlan.id,
						"prorate":false,
						"end_of_term":true,
					}
				};
			}else if(this.couponCode !== ''){
				data = {
					'billing':{
						'plan_id':this.upgradeToPlan.id,
						'prorate':true,
						'end_of_term':false,
						"coupon":this.couponCode
					}
				};
			}else{
				data = {
					'billing':{
						'plan_id':this.upgradeToPlan.id,
						'prorate':true,
						'end_of_term':false,
						'status':true
					}
				};
			}
			this.changeToPlan.emit(data);
			this.isChangePlan.emit(true);
			jQuery('#upgrade-plan-popup').modal('hide');
			setTimeout(function () {
				jQuery('#cc-modal').modal('show');
			}, 1000);
		}
		else{
			this.isChangePlan.emit(false);
			this.changeToPlan.emit({});
			this.changeSubscription();
		}
	}
	changeSubscription(){
		let data:any = {};
		if(this.upgradeToPlan.id === 'starter'){
			data = {
				'billing':{
					"plan_id":this.upgradeToPlan.id,
					"prorate":false,
					"end_of_term":true,
				}
			};
		}else if(this.couponCode !== ''){
			data = {
				'billing':{
					'plan_id':this.upgradeToPlan.id,
					'prorate':true,
					'end_of_term':false,
					"coupon":this.couponCode
				}
			};
		}else{
			data = {
				'billing':{
					'plan_id':this.upgradeToPlan.id,
					'prorate':true,
					'end_of_term':false
				}
			};
		}
		let self = this;
		jQuery('.btnMakePayment').html('please Wait...').attr('disabled',true);
		let changeSubscription = self._membershipService.updateSubscription(data)
			.subscribe(
				(success: any) => {
					jQuery('#upgrade-plan-popup').modal('hide');
					self.getPlanSubscription();
					jQuery('.btnMakePayment').html('Make Payment').attr('disabled',false);
					this.error = false;
					this.errorMessage = '';
					jQuery('#changeSubscriptionMessage').html('Plan Subscribed Successfully');

					let storage = JSON.parse(this._cookieService.readCookie('storage'));
									storage.company.billing.chargebee_plan_id = data.billing.plan_id;
									this._cookieService.eraseCookie('storage');
									this._cookieService.createCookie('storage',JSON.stringify(storage),3);

					/*==== Tracking code and Lead Dyno Purchase code goes here ====*/
					this.kmqAmountDue = 0;
					this.kmqAmountPaid = 0;
					this.kmqCreditsApplied = 0;
					this.kmqTotalAmount = 0;
					if(success.invoice && success.invoice.amount_paid !== 0){
						this.kmqAmountPaid = Math.round(success.invoice.amount_paid/100);
					}
					if(success.invoice && success.invoice.credits_applied !== 0) {
						this.kmqCreditsApplied = Math.round(success.invoice.credits_applied/100);
					}
					if(success.invoice && success.invoice.amount_due !== 0) {
						this.kmqAmountDue = Math.round(success.invoice.amount_due/100);
					}
					if(success.invoice && success.invoice.sub_total !== 0) {
						this.kmqTotalAmount = Math.round(success.invoice.sub_total/100);
					}
					let kmqData = {
	      				'Total Amount':this.kmqTotalAmount,
	      				'Amount Paid': this.kmqAmountPaid,
	      				'Credits Applied': this.kmqCreditsApplied,
	      				'Amount Due':this.kmqAmountDue,
	      				'Plan':this.upgradeToPlan.name
	      			};
	      			let subs = success.subscription ? success.subscription : null;
	      			if(subs) {
	      				let user_status = 'Trial';
		                switch (subs.status) {
		                    case 'in_trial':
		                        user_status = 'Trial';
		                        break;
		                    case 'active':
		                        user_status = 'Paid';
		                        if (subs.plan_id === 'starter') { user_status = 'Free'; }
		                        break;
		                }
		                let icd = JSON.parse(localStorage.getItem('icd'));
		                if(icd) {
		                	icd.subscription_status = user_status;
		                	localStorage.setItem('icd', JSON.stringify(icd));
		                }
		                window.Intercom('update', { 'subscription_status': user_status })
	      			}
	      			if(window.location.href.indexOf('outgrow.co') >= 0) {
	      				fbq('track', 'Purchase', {value: this.kmqTotalAmount.toString(), currency: 'USD'});
					}
					ga('markettingteam.send', 'event', 'Settings', 'Submit', 'SettingsPlanChanged');
	      			_kmq.push(['record', 'Settings Plan Changed', kmqData]);

	      			/*let url = window.location.href;
					if(url.toLowerCase().indexOf("outgrow.co") >= 0) {
						let leadDynoData = {
		      				purchase_code: 'Tinker',
		      				purchase_amount: 0
		      			};
		      			if(success.invoice) {
		      				leadDynoData.purchase_code = success.invoice.id;
		      				leadDynoData.purchase_amount = Math.round(success.invoice.sub_total/100);
		      			}
						if(this.membershipCancel) {
							LeadDyno.recordCancellation(success.customer.email);
						}
						else {
							LeadDyno.recordPurchase(success.customer.email, leadDynoData);
						}
					}*/
					/*==================*/

					window.toastNotification('Plan Subscribed Successfully');
				},
				(error:any) => {
					this.error = true;
					jQuery('.btnMakePayment').html('Make Payment').attr('disabled',false);
					this.errorMessage = error.error.err_message;
					changeSubscription.unsubscribe();
				}
			);
	}
	showEstimateModal(plan:Plans,cancel?:boolean){
		if(cancel) {
			this.membershipCancel = true;
		}
		this.isChangePlan.emit(false);
		this.getUpgradeEstimate(plan);
		jQuery('#upgrade-plan-popup').modal('show');
	}
	showEstimateModalAddon(){
		this.getAddonEstimate(this.runningPlanDetail);
		jQuery('#addon-popup').modal('show');
	}
	cancelMembeship(){
		jQuery('#cancelMembership span').text('Please wait...').attr('disabled',true);
		let cancelMembership = this._membershipService.cancelMembership()
		.subscribe(
			(success: any) => {
				jQuery('#cancelMembership span').text('Your request to cancel membership has been sent');
				jQuery('#cancelMembership').addClass('btn-disable');
				window.toastNotification('Your Request to Cancel the membership have been received. You will be updated via email');
			},
			(error:any) => {
				this.error = true;
				cancelMembership.unsubscribe();
			}
		);
	}

	updateAddon(){
		let self = this;
		this.error = false;
		this.errorMessage = '';
		let data = {
			'plan_id':self.runningPlanDetail.id
		}
		jQuery('#btnMakePaymentA').html('please Wait...').attr('disabled',true);
		let changeSubscription = self._membershipService.updateAddon(data)
		.subscribe(
			(success:any)=>{
				localStorage.setItem('lodashAuthToken', JSON.stringify(success));
				let storage = JSON.parse(self._cookieService.readCookie('storage'));
				storage.company.addon = success.addon;
        storage.company.current_usage = success.current_usage;
        storage.company.current_limit = success.current_limit;
        this._cookieService.createCookie('storage', JSON.stringify(storage), 3);
				jQuery('#addon-popup').modal('hide');
				jQuery('#btnMakePaymentA').html('Add Addon').attr('disabled',false);
				window.toastNotification('Addon Added successfully');
				self.getAddonAppliedDetails();
			},
			(error:any)=>{
				console.log('error',error);

				this.error = true;
				jQuery('#btnMakePaymentA').html('Add Addon').attr('disabled',false);
				this.errorMessage = error.error.err_message;
				changeSubscription.unsubscribe();
			}
		);
	}
	getAddonEstimate(plan:Plans){
		let self = this;
		this.loading = true;
		let data = {
			'plan_id':plan.id,
			'quantity':self.hasAddon + 1
		}
		let getAddonEstimate = self._membershipService.getAddonEstimate(data)
			.subscribe(
				(success: any) => {
					self.estimation = new Estimate(success.estimate);
					self.loading = false;
					this.error = false;
					this.errorMessage = '';
				},
				(error:any) => {
					console.log('estim error',error);
					this.error = true;
					getAddonEstimate.unsubscribe();
					self.loading = false;
				}
			);
	}
	getUpgradeEstimate(plan:Plans){
		this.loading = true;
		this.upgradeToPlan = plan;
		let cycle = this.upgradeToPlan.id.split('_')[1];
		if(cycle === 'm')
			this.upgradeBillingCycle = 'Monthly';
		else if(cycle === 's')
			this.upgradeBillingCycle = 'Half Yearly';
		else if(cycle === 'y')
			this.upgradeBillingCycle = 'Yearly';
		this.couponCode = '';
		if(jQuery('#couponInput:text').val() && this.upgradeToPlan.id !='starter'){
			this.couponCode = this.couponForm.value.couponInput;
			this.couponCodeModal = '';
		}
		let data = {};
		if(plan.id === 'starter'){
			data = {
				'plan_id':plan.id,
				'prorate':false,
				'end_of_term':true
			};
		}else if(this.couponCode !== ''){
			data = {
				'plan_id':plan.id,
				'prorate':true,
				'end_of_term':false,
				"coupon":this.couponCode
			};
		}else{
			data = {
				'plan_id':plan.id,
				'prorate':true,
				'end_of_term':false
			};
		}
		let self = this;
		let getUpgradeEstimate = self._membershipService.getUpdateEstimate(data)
			.subscribe(
				(success: any) => {

					self.estimation = new Estimate(success.estimate);
					self.loading = false;
					this.error = false;
					this.errorMessage = '';
					let due_amount :any;
					due_amount = self.estimation.next_invoice_estimate ?
							     self.estimation.next_invoice_estimate.total:
								 self.estimation.invoice_estimate?self.estimation.invoice_estimate.total:'';
					if(due_amount===0){
						this.isDowagrade = true;
					}else{
						this.isDowagrade = false;
					}
					console.log(data,'success------------------',success);
				},
				(error:any) => {
					this.error = true;
					jQuery('.btnMakePayment').html('Make Payment').attr('disabled',false);
					if(error.error.err_message.indexOf(this.couponCode) != -1)
						this.errorMessage = 'Invalid Coupon ' + this.couponCode;
					else
						this.errorMessage = error.error.err_message;
					this.couponCode = '';
					getUpgradeEstimate.unsubscribe();
					self.loading = false;
				}
			);
	}
	callGA(opt: string) {
	    switch (opt) {
	    	case "SELECTPLAN":
	    		ga('markettingteam.send', 'event', 'Settings', 'Click', 'SelectPlan');
	    		_kmq.push(['record', 'Settings Select Plan Click']);
	        	break;

	      	case "MAKEPAYMENT":
	      		ga('markettingteam.send', 'event', 'Settings', 'Click', 'MakePayment');
	      		_kmq.push(['record', 'Settings Make Payment Click']);
	        	break;

        	case "CHANGECYCLE":
	      		ga('markettingteam.send', 'event', 'Settings', 'Click', 'ChangeCycle');
	      		_kmq.push(['record', 'Settings Change Cycle Click']);
	        	break;

        	case "CHANGEPLAN":
	      		ga('markettingteam.send', 'event', 'Settings', 'Click', 'ChangePlan');
	      		_kmq.push(['record', 'Settings Change Plan Click']);
	        	break;
	    }
	}

	expand(pId:any){
		console.log('pId',pId);
		if(jQuery('#'+pId).hasClass('rs-hide')) {
			jQuery('#' + pId).removeClass('rs-hide');
			jQuery('#' + pId + '_arrow').html('keyboard_arrow_up');
			jQuery('#' + pId + '_more').html('Less Info');
		}
		else {
			jQuery('#' + pId).addClass('rs-hide');
			jQuery('#' + pId + '_arrow').html('keyboard_arrow_down');
			jQuery('#' + pId + '_more').html('More Info');
		}
	}

	contactUs(){
		jQuery(document).find('.intercom-launcher').click();
		// console.log('clicked');
	}

	ngOnChanges(){
		this.subs.push(this.getPlanSubscription());
		this.subs.push(this.getAllPlans());
		this.subs.push(this.getAddons());
	}
	ngOnDestroy() {
		this.subs.forEach(sub => sub.unsubscribe());
	}
}



