import {Component, OnInit, ViewChild, AfterViewInit} from '@angular/core';
import { SubscriptionComponent } from './subscription/index';
import { FormGroup, FormBuilder ,Validators } from '@angular/forms';
import { InvoiceComponent } from './invoice/index';
import {MembershipService, CookieService, Script} from './../../../shared/services/index';
import {Card} from './../../../shared/models/currentPlan';
declare var jQuery : any;
declare var moment:any;
declare var ga: any;
declare var _kmq: any;
declare var window: any;
declare var fbq: any;
declare var LeadDyno: any;

@Component({
	selector: 'og-membership',
	template: require('./membership.component.html'),
	styles: [require('./membership.component.css')]
})

export class MembershipComponent implements OnInit, AfterViewInit{
	@ViewChild(SubscriptionComponent) subsComp:SubscriptionComponent;
	@ViewChild(InvoiceComponent) invoice:InvoiceComponent;
	monthsArray:any = [];
	yearsArray:any = [];
	currentYear:any = '';
	setupPaymentForm:FormGroup;
	error:boolean = false;
	errorMessage: string = '';
	CardDetail: Card = new Card(null);
	card_status:string = '';
	userRole: string = 'MANAGER';
	cardType: string = '';
	cardValid:boolean = false;
	isChangePlan:boolean = false;
	changeToPlan:any = '';

	kmqAmountDue: any;
	kmqAmountPaid:any;
	kmqCreditsApplied: any;
	kmqTotalAmount:any;

	constructor(
		private fb : FormBuilder,
		private _membershipService : MembershipService,
		private _script :Script,
		private _cookieService:CookieService){
		this.userRole = localStorage.getItem('role');
	};

	ngOnInit() {
		jQuery('.modal-backdrop').remove();
    //CHANGE IT LATER
    jQuery('body').removeClass('modal-open');
		this.monthsArray = [ '01', '02', '03','04','05', '06', '07','08','09', '10', '11','12' ];
		this.yearsArray = [ '2016', '2017', '2018','2019','2020', '2021', '2022','2023','2024', '2025', '2026','2027'];
		//this.currentMonth =  (new Date).getMonth()+1;
		this.currentYear =  (new Date).getFullYear();
		//this.getPaymentDetails();
		this.setupPaymentForm = this.fb.group({
			cardNumber1 : ['', Validators.compose([Validators.required,Validators.minLength(4),Validators.pattern('^[0-9]*$')])],
			cardNumber2 : ['', Validators.compose([Validators.required,Validators.minLength(4),Validators.pattern('^[0-9]*$')])],
			cardNumber3 : ['', Validators.compose([Validators.required,Validators.minLength(4),Validators.pattern('^[0-9]*$')])],
			cardNumber4 : ['', Validators.compose([Validators.required,Validators.minLength(2),Validators.pattern('^[0-9]*$')])],
			nameOnCard : ['', Validators.compose([Validators.required, Validators.minLength(3)])],
			cvv : ['', Validators.compose([Validators.required,Validators.minLength(3),Validators.pattern('^[0-9]*$') ])],
			cardMonth: ['', Validators.compose([Validators.required,Validators.minLength(1),Validators.pattern('^[0-9]*$')])],
			cardYear: ['', Validators.compose([Validators.required,Validators.minLength(4),Validators.pattern('^[0-9]*$')])]
		});
		jQuery('.modal').on('hidden.bs.modal', function(){
		  this.errorMessage = '';
		  this.error = 'false';
		});
		jQuery.material.init();
		this.userRole = localStorage.getItem('role');
	}

	 ngAfterViewInit() {
      this._script.load('cardValidator')
        .then((data)=> {
          //console.log('Scripts Loadedaaaaaaaaaaaaaaaa', data);
        })
        .catch((error)=>{
          //console.log('Script failed to load',error);
        });
  }

	onChangeCardNumber(cardNum:any){
		let self = this;
		jQuery(cardNum).validateCreditCard(function(result:any) {
			if(result.card_type != null){
				self.cardType = result.card_type.name;
				if(result.length_valid && result.luhn_valid && result.valid)
					self.cardValid = true;
			}
		});
		var pattern = /[a-z\s',\."/{}()[\]]/gi;
		var stringnumber = cardNum.value.replace(pattern,'');
		cardNum.value = stringnumber;
		if(cardNum.value.length === 4){
			jQuery(cardNum).next('input').focus();
		}
	}

	setupPayment(){
		let cardData = {
			'cardNumber':this.setupPaymentForm.value.cardNumber1
						+ this.setupPaymentForm.value.cardNumber2
						+ this.setupPaymentForm.value.cardNumber3
						+ this.setupPaymentForm.value.cardNumber4,
			'cvv':this.setupPaymentForm.value.cvv,
			'cardMonth':this.setupPaymentForm.value.cardMonth,
			'cardYear':this.setupPaymentForm.value.cardYear,
		};
		this.error = false;
		this.errorMessage = '';
		let self = this;
		jQuery('#btnSetupCard span').text('Please wait...').attr('disabled',true);
		let setupPayment = self._membershipService.resetPayment(cardData)
			.subscribe(
				(success: any) => {
					self.cardType = '';
					self.cardValid = false;
					this.CardDetail = new Card(success.card);
					this.card_status = success.customer.card_status;
					this.error = false;
					this.errorMessage = '';
					if(this.isChangePlan && this.changeToPlan != '' && this.changeToPlan.billing.status){
						let changeSubscription = self._membershipService.updateSubscription(this.changeToPlan)
							.subscribe(
								(success: any) => {
									this.subsComp.getPlanSubscription();
									this.invoice.getInvoices();
									this.error = false;
									this.errorMessage = '';
									let storage = JSON.parse(this._cookieService.readCookie('storage'));
									storage.company.billing.chargebee_plan_id = this.changeToPlan.billing.plan_id;
									this._cookieService.eraseCookie('storage');
									this._cookieService.createCookie('storage',JSON.stringify(storage),3);

									jQuery('#cc-modal').modal('hide');
									jQuery('#btnSetupCard span').text('Submit').attr('disabled',false);
									jQuery('#btnSetupCard').html('Submit').attr('disabled',false);
									jQuery('#cc-modal input').val('');
									jQuery('#btnSetupCard span').text('Make Payment').attr('disabled',false);

									/*==== Analytics Tracking and Payment Tracking code here ====*/
									this.kmqAmountDue = 0;
									this.kmqAmountPaid = 0;
									this.kmqCreditsApplied = 0;
									this.kmqTotalAmount = 0;
									if(success.invoice && success.invoice.amount_paid !== 0){
										this.kmqAmountPaid = Math.round(success.invoice.amount_paid/100);
									}
									if(success.invoice && success.invoice.credits_applied !== 0) {
										this.kmqCreditsApplied = Math.round(success.invoice.credits_applied/100);
									}
									if(success.invoice && success.invoice.amount_due !== 0) {
										this.kmqAmountDue = Math.round(success.invoice.amount_due/100);
									}
									if(success.invoice && success.invoice.sub_total !== 0) {
										this.kmqTotalAmount = Math.round(success.invoice.sub_total/100);
									}
									let kmqData = {
										'Total Amount':this.kmqTotalAmount,
										'Amount Paid': this.kmqAmountPaid,
										'Credits Applied': this.kmqCreditsApplied,
										'Amount Due':this.kmqAmountDue,
										'Plan':success.invoice.line_items[0].description
									};
									if(window.location.href.indexOf('outgrow.co') >= 0) {
										fbq('track', 'Purchase', {value: this.kmqTotalAmount.toString(), currency: 'USD'});
									}
									ga('markettingteam.send', 'event', 'Settings', 'Submit', 'SettingsPlanChanged');
									_kmq.push(['record', 'Settings Plan Changed', kmqData]);

									let url = window.location.href;
									if(url.toLowerCase().indexOf("outgrow.co") >= 0) {
										let leadDynoData = {
											purchase_code: 'Tinker',
											purchase_amount: 0
										};
										if(success.invoice) {
											// leadDynoData.purchase_code = success.invoice.id;
											// leadDynoData.purchase_amount = Math.round(success.invoice.sub_total/100);
										}
										LeadDyno.recordPurchase(success.customer.email, leadDynoData);
									}
									/*=====================*/
									window.toastNotification('Payment successfully done and plan is changed.');
									this.isChangePlan = false;
									this.changeToPlan = '';
								},
								(error:any) => {
									this.error = true;
									this.errorMessage = 'Your card was declined.';
									jQuery('#btnSetupCard span').text('Make Payment').attr('disabled',false);
									//jQuery('#btnSetupCard').html('Make Payment').attr('disabled',false);
									changeSubscription.unsubscribe();
								}
							);
					}
					if(this.isChangePlan && this.changeToPlan!='' && !this.changeToPlan.billing.status){
						jQuery('#btnSetupCard span').text('Please Wait...').attr('disabled',true);
						//jQuery('#btnSetupCard span').text('Please Wait...').attr('disabled',true);
						this.reactivate();
					}
					else{
						jQuery('#cc-modal').modal('hide');
						jQuery('#btnSetupCard span').text('Submit').attr('disabled',false);
						//jQuery('#btnSetupCard').html('Submit').attr('disabled',false);
						jQuery('#cc-modal input').val('');
						window.toastNotification('Payment successfully added');
					}
					this.errorMessage = '';
					/*---- Tracking code goes here ----*/
					if(window.location.href.indexOf('outgrow.co') >= 0) {
						fbq('track', 'AddPaymentInfo');
					}
					ga('markettingteam.send', 'event', 'Settings', 'Submit', 'Settings Add Payment Method');
					_kmq.push(['record', 'Settings Payment Method Added']);
					/*---------------------------------*/
				},
				(error:any) => {
					this.error = true;
					this.errorMessage = error.error.err_message;
					console.log('>>>>>>>>>>>>>>>', this.isChangePlan);
					if(this.isChangePlan)
						jQuery('#btnSetupCard span').text('Make Payment').attr('disabled',false);
						//jQuery('#btnSetupCard').html('Make Payment').attr('disabled',false);
					else
						jQuery('#btnSetupCard span').text('Submit').attr('disabled',false);
						//jQuery('#btnSetupCard').html('Submit').attr('disabled',false);
					setupPayment.unsubscribe();
				}
			);
	}

	reactivate(){
		let reactivateMembership = this._membershipService.activateNow()
		.subscribe(
			(success: any) => {
				window.toastNotification('You have Successfully Reactivated ');
				//this._cookieService.createCookie('filepicker_token_json', JSON.stringify(response.companyAccess), 3);
				let membership = JSON.parse(this._cookieService.readCookie('filepicker_token_json'));
				membership[1].value = success.subscription.status;
				this._cookieService.eraseCookie('filepicker_token_json');
				this._cookieService.createCookie('filepicker_token_json',JSON.stringify(membership),3);
				let storage = JSON.parse(this._cookieService.readCookie('storage'));
				storage.company.billing.chargebee_plan_id = success.subscription.plan_id;
				this._cookieService.eraseCookie('storage');
				this._cookieService.createCookie('storage',JSON.stringify(storage),3);
				window.location.reload();
			},
			(error:any) => {
				this.error = true;
				this.errorMessage = 'Subscription cannot be re-activated as your card is decline';
				jQuery('#btnSetupCard span').text('Make Payment').attr('disabled',false);
				reactivateMembership.unsubscribe();
			}
		);
	}

	closeLayover() {
		setTimeout(function() {
			jQuery('.float-changes-updated').fadeIn()
				.animate({bottom:-50}, 800, function() {});
			}, 4000);
	}
}



