import { Validators, FormBuilder, FormGroup } from '@angular/forms';
import { Component, OnInit } from '@angular/core';
import { Router} from '@angular/router';
import { LoggedInService,UserService } from '../../../../shared/services/index';

declare var jQuery : any;
declare var ga: any;
declare var _kmq:any;
declare var window: any;

@Component({
  selector: 'og-change-password',
	providers: [UserService],
  template: require('./changepassword.component.html'),
	styles: [require('./changepassword.component.css')]
})

export class ChangePasswordComponent implements OnInit {
  changePasswordForm: FormGroup;
  success:boolean = false;
  error: boolean = false;
  Message: string;
  old_password:any;
  new_password:any;
  confirm_password:any;

  constructor(
    public _userService: UserService,
    public router: Router,
    private fb:FormBuilder,
    private loggedInService: LoggedInService
  ) {
  }

  ngOnInit() {
    this.changePasswordForm = this.fb.group({
      old_password: [this.old_password, Validators.compose([
        Validators.required,Validators.minLength(8)
      ])],
      new_password: [this.new_password, Validators.compose([
        Validators.required,Validators.minLength(8)
      ])],
      confirm_password: [this.confirm_password, Validators.compose([
        Validators.required,Validators.minLength(8)
      ])]
    });
  }

  matchPasswords(passwordKey: string, confirmPasswordKey: string) {
    return (group: FormGroup) => {
      let passwordInput = group.controls[passwordKey];
      let passwordConfirmationInput = group.controls[confirmPasswordKey];
      if (passwordInput.value !== passwordConfirmationInput.value) {
        this.error = true;
        this.Message = 'Passwords do not match';
        return passwordConfirmationInput.setErrors({notEquivalent: true});
      }else {
        this.error = false;
      }
    };
  }

  setFalse() {
    this.success = false;
    this.error = false;
  }

  onSubmit(value: any) {
    jQuery('#updatePassword').text('Please Wait...');
    jQuery('#updatePassword').attr('disabled',true);
		value = this.changePasswordForm.value;
    let new_password = value.new_password;
    let old_password = value.old_password;
    let confirm_password = value.confirm_password;

    if (new_password !== confirm_password) {
      this.error = true;
      this.success = false;
      this.Message = "Your passwords don't match. Please retype your password to confirm";
      jQuery('#updatePassword').text('Update');
      // jQuery('#updatePassword').mouseOut();
    }else {
      if(new_password === confirm_password) {
      this._userService.updatePassword(old_password,new_password)
        .subscribe(
          (response: any) => {
            /*---- Tracking code goes here ----*/
            ga('markettingteam.send', 'event', 'Settings', 'Submit', 'UpdatePassword');
            _kmq.push(['record', 'Settings Password Updated']);
            /*---------------------------------*/
            window.toastNotification('Password Updated Successfully');
            this.resetFields();
            this.success = true;
            this.error = false;
            jQuery('#updatePassword').text('Update');
            jQuery('#updatePassword').attr('disabled',false);
            // jQuery('#updatePassword').mouseOut();
          },
          (response:any) => {
            this.Message = response.error.err_message;
            this.error = true;
            this.success = false;
            jQuery('#updatePassword').text('Update');
            jQuery('#updatePassword').attr('disabled',false);
            // jQuery('#updatePassword').mouseOut();
          }
        );
      }else {
        this.error = true;
        this.Message = 'Old Password and New Password Do not match';
        jQuery('#updatePassword').text('Update');
        jQuery('#updatePassword').attr('disabled',false);
        // jQuery('#updatePassword').mouseOut();
      }
    }
    setTimeout(function(){
      this.setFalse();
    }.bind(this), 9000);
  }

  resetFields() {
    jQuery('input').val('');
  }
  resetvalidation() {
    this.error = false;
    this.Message ='';
    jQuery('#updatePassword').attr('disabled', false);
  }

  callGA(opt: string){
    switch (opt) {
      case "UPDATEPASS":
        ga('markettingteam.send', 'event', 'Settings', 'Click', 'UpdatePassword');
        _kmq.push(['record', 'Settings Password Clicked']);
        break;
    }
  }
}



