import { Component, OnInit, EventEmitter, AfterViewInit} from '@angular/core';
import {MembershipService, Script, CookieService} from '../../../../shared/services/index';
import {Customer,Card} from '../../../../shared/models/currentPlan';
import {Validators, FormBuilder, FormGroup} from '@angular/forms';


declare var ga: any;
declare var jQuery : any;
declare var _kmq: any;
declare var window: any;

@Component({
	selector : 'og-payment',
	template: require('./payment.component.html'),
	styles: [require('./payment.component.css'),require('./../../../../../assets/css/team-settings.css'),require('./../../../../../assets/css/sahil-hover.css'), require('./../../../../../assets/css/custom-material.css')],
	outputs:['CardDetailChanged'],
	inputs:['CardDetail','card_status','userRole', 'cardType']
})

export class PaymentComponent implements OnInit, AfterViewInit {
	BillingDetails: Customer = new Customer(null);
	BillingAddress:any = '';
	CardDetail: Card = new Card(null);
	error:boolean = false;
	errorMessage: string = '';
	billingAddressForm:FormGroup;
	CardDetailChanged = new EventEmitter<Card>();
	name:string = '';
	email:string = '';
	address:string = '';
	city:string = '';
	state:string = '';
	country:string = '';
	zip:string = '';
	currentMonth:any=(new Date).getMonth()+1;
    currentYear:any=(new Date).getFullYear();
    cardExpired:boolean = false;
    CardWillExpire:boolean = false;
    userRole:string = 'MANAGER';
    cardType:string = '';
    loadingPayDet:boolean = true;
	constructor(
			private _membershipService: MembershipService,
			public fb: FormBuilder,
			private _cookieService: CookieService,
			private _script: Script) {};
	ngOnInit() {
		jQuery('.modal').on('hidden.bs.modal', function(){
			this.error = false;
			this.errorMessage = '';
		});
		this.getPaymentDeatils();
		this.billingAddressForm = this.fb.group({
			inputName: [this.BillingDetails.first_name, Validators.compose([Validators.required, Validators.minLength(3)])],
			inputAddress: [this.BillingAddress.line1, Validators.compose([Validators.required, Validators.minLength(3)])],
			inputCity: [this.BillingAddress.city, Validators.compose([Validators.required, Validators.minLength(3)])],
			inputState: [this.BillingAddress.state, Validators.compose([Validators.required, Validators.minLength(3)])],
			inputCountry: [this.BillingAddress.country, Validators.compose([Validators.required])],
			inputZipCode: [this.BillingAddress.zip, Validators.compose([Validators.required, Validators.minLength(6), Validators.pattern('^[a-zA-Z0-9]*$')])]
		});
		this.userRole = this._cookieService.readCookie('role');
	}

	ngAfterViewInit() {
       this._script.load('cardValidator','timeZoneMin')
        .then((data)=>{
            //console.log('Scripts Loaded', data);
        })
        .catch((error)=>{
           //console.log('Script failed to load',error);
        });
	}
	 getPaymentDeatils(){
	 	let self = this;
	 	self.loadingPayDet = true;
		let getPaymentDeatils = self._membershipService.getBillingAddress()
			.subscribe(
				(success: any) => {
				  console.log('getpaydet',success);
					this.BillingDetails = new Customer(success.customer);
					this.BillingAddress = this.BillingDetails.billing_address;
					this.CardDetail = new Card(success.card);
					this.viewBillingAddress();
					this.CardDetailChanged.emit(this.CardDetail);
					self.loadingPayDet = false;
				},
				(error:any) => {
					getPaymentDeatils.unsubscribe();
					self.loadingPayDet = true;
				}
			);
	 }
	 updateBillingAddress(){
	 	let self = this;
	 	jQuery('#btnBilling').html('please wait...').attr('disables',true);
		let getPaymentDeatils = self._membershipService.setupBilling(this.billingAddressForm.value)
			.subscribe(
				(success: any) => {
					jQuery('#billing-detail').modal('hide');
					this.BillingDetails = new Customer(success.customer);
					this.BillingAddress = this.BillingDetails.billing_address;
					this.CardDetail = new Card(success.card);
					this.CardDetailChanged.emit(this.CardDetail);
					this.viewBillingAddress();
					jQuery('#btnBilling').html('Save').attr('disables',false);
					this.error = false;
					this.errorMessage = '';
					jQuery('#BillingAddressMessage').html('Billing address updated successfully');
					/*---- Tracking code goes here ----*/
					ga('markettingteam.send', 'event', 'Settings', 'Submit', 'Settings Billing Info Edited');
					_kmq.push(['record', 'Settings Billing Info Edited']);
					/*---------------------------------*/
					window.toastNotification('Billing address updated successfully');
				},
				(error:any) => {
					this.error = true;
					this.errorMessage = error.error.err_message;
					jQuery('#btnBilling').html('Save').attr('disables',false);
					getPaymentDeatils.unsubscribe();
				}
			);
	 }

	 viewBillingAddress(){
	 	this.name = this.BillingDetails.first_name;
		this.email = this.BillingDetails.email;
		this.address = this.BillingAddress.line1;
		this.city = this.BillingAddress.city;
		this.state = this.BillingAddress.state;
		this.country = this.BillingAddress.country;
		this.zip = this.BillingAddress.zip;

		// if(this.CardDetail.expiry_year < this.currentYear)
		// 	this.cardExpired = true;
		// else if(this.CardDetail.expiry_year === this.currentYear && this.CardDetail.expiry_month < this.currentMonth)
		// 	this.cardExpired = true;
		// if(this.CardDetail.expiry_year === this.currentYear && this.CardDetail.expiry_month === this.currentMonth)
		// 	this.CardWillExpire = true;
	 }

	 contactUs(){
		jQuery('.intercom-launcher').trigger('click');
	}

	callGA(opt: string) {
		switch (opt) {
			case "EDITBILLING":
				ga('markettingteam.send', 'event', 'Settings', 'Click', 'Settings Edit Billing Info');
				_kmq.push(['record', 'Settings Edit Billing Info Click']);
				break;

			case "PAYMENTCLICK":
				if(this.BillingDetails.card_status == 'no_card') {
					ga('markettingteam.send', 'event', 'Settings', 'Click', 'Settings Add Payment Method');
					_kmq.push(['record', 'Settings Add Payment Method Click']);
				}
				else {
					ga('markettingteam.send', 'event', 'Settings', 'Click', 'Settings Reset Payment Method');
					_kmq.push(['record', 'Settings Reset Payment Method Click']);
				}
				break;
		}
	}
}



